import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController } from 'ionic-angular';

/**
 * Generated class for the MainPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-main',
  template : '<ion-menu [content]="content" id="menu1"> <ion-header> <ion-toolbar color="secondary"> <ion-title>Menu 1</ion-title> </ion-toolbar> </ion-header><ion-content><ion-list><button ion-item menuClose="menu1" detail-none>Close Menu 1</button></ion-list></ion-content></ion-menu><ion-menu [content]="content" id="menu2"><ion-header><ion-toolbar color="danger"><ion-title>Menu 2</ion-title></ion-toolbar></ion-header><ion-content><ion-list><button ion-item menuClose="menu2" detail-none>Close Menu 2</button></ion-list></ion-content></ion-menu><ion-nav [root]="rootPage" #content swipeBackEnabled="false"></ion-nav>',
  templateUrl: 'main.html',
})
export class MainPage {

  activeMenu: string;
  tab1Root = "InfoPage";
  tab2Root = "DetailsPage";


  constructor(public navCtrl: NavController, public navParams: NavParams, public menu: MenuController) {
    this.menu1Active();
  }
  menu1Active() {
    this.activeMenu = 'menu1';
    this.menu.enable(true, 'menu1');
    this.menu.enable(false, 'menu2');
  }
  menu2Active() {
    this.activeMenu = 'menu2';
    this.menu.enable(false, 'menu1');
    this.menu.enable(true, 'menu2');
  }

  infiniteDirect()
  {
    this.navCtrl.push("InfoPage");
  }

  sqlDirect(){
    this.navCtrl.push("SqlPage");
  }

  virtualDirect()
  {
    this.navCtrl.push("VirinfoPage");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MainPage');
  }

}



